


















import Vue from'vue';
import {Component} from'vue-property-decorator';

const map: {[key: string]: string}={
  'tag name duplicated':''
}

@Component
export default class Tags2 extends Vue {
  selectedTags: string[] = [];

  get tagList(){
    return this.$store.state.tagList;
  }
  created(){
    this.$store.commit('fetchTags');
  }

  select(tag: string) {
    const index = this.selectedTags.indexOf(tag)//selectedTags
    if (index >= 0) {
      this.selectedTags.splice(index, 1)
    } else {
      this.selectedTags = []
      this.selectedTags.push(tag);
    }
    this.$emit('update:value',this.selectedTags)//selectedTagsMoney
  }

  create() {
    const name = window.prompt('');
    if (!name) {
     return  window.alert('');
    }
      this.$store.commit('createTag',name);
    if(this.$store.state.createTagError){
      window.alert(map[this.$store.state.createTagError.message ||'']);
    }
    }
  }

